@charset "UTF-8";
.etnos-blog--sidebar {
  position: sticky;
  top: 30px;
  --page-bg: var(--c-alter2);
  --post-br: 7px;
  --post-cat-bg: var(--c-active);
  --post-bg: var(--c-light);
}
.etnos-blog--sidebar .widget {
  border-radius: var(--post-br);
  background: var(--post-bg);
  box-shadow: rgba(var(--ca-dark), 0.05) 0 0 15px 0;
  padding: 30px;
  margin-bottom: 23px;
}
@media screen and (min-width: 576px) {
  .etnos-blog--sidebar .widget {
    margin-bottom: 15px;
  }
}
@media only screen and (max-width: 767px) {
  .etnos-blog--sidebar .widget {
    padding: 0 0 15px;
  }
}
.etnos-blog--sidebar .widget-title {
  margin-bottom: 20px;
}
.etnos-blog--sidebar .widget_search form > div {
  color: var(--c-dark);
  display: flex;
}
.etnos-blog--sidebar .widget_search form > div label {
  display: none;
}
.etnos-blog--sidebar .widget_search input[type=search],
.etnos-blog--sidebar .widget_search input[type=text] {
  border: 1px solid rgba(var(--ca-dark), 0.3);
  line-height: 35px;
  padding: 5px 20px;
  width: calc(100% - 65px);
  border-top-left-radius: var(--post-br);
  border-bottom-left-radius: var(--post-br);
}
.etnos-blog--sidebar .widget_search button[type=submit],
.etnos-blog--sidebar .widget_search input[type=submit] {
  border: 1px solid var(--c-active);
  position: relative;
  z-index: 10;
  border-top-right-radius: var(--post-br);
  border-bottom-right-radius: var(--post-br);
  font-weight: 600;
  padding: 5px 15px;
  background-color: var(--c-active);
  color: var(--c-light);
  cursor: pointer;
  transition: all 0.3s ease;
}
.etnos-blog--sidebar .widget_search button[type=submit]:hover,
.etnos-blog--sidebar .widget_search input[type=submit]:hover {
  background-color: var(--c-light);
  border-color: var(--c-active);
  color: var(--c-active);
}
.etnos-blog--sidebar .widget_search .submit-wrap {
  position: relative;
  display: inline-block;
  vertical-align: top;
}
.etnos-blog--sidebar .widget_search .submit-wrap::before {
  content: "\f2f5";
  font-family: "Ionicons";
  position: absolute;
  z-index: 1;
  font-size: 20px;
  height: 42px;
  width: 52px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--c-light);
  border: 1px solid var(--c-dark);
  background: var(--c-dark);
  transition: all 300ms ease;
}
.etnos-blog--sidebar .widget_search .submit-wrap:hover::before {
  background: transparent;
  color: var(--c-dark);
}
.etnos-blog--sidebar .etnos-widget-popular--item {
  display: flex;
}
.etnos-blog--sidebar .etnos-widget-popular--item:not(:last-of-type) {
  margin-bottom: 30px;
}
@media only screen and (max-width: 767px) {
  .etnos-blog--sidebar .etnos-widget-popular--item:not(:last-of-type) {
    margin-bottom: 15px;
  }
}
.etnos-blog--sidebar .etnos-widget-popular--item:first-of-type .etnos-widget-popular--image span {
  background: var(--c-dark);
}
.etnos-blog--sidebar .etnos-widget-popular--image {
  position: relative;
  min-width: 30px;
  min-height: 30px;
  margin-right: 20px;
}
.etnos-blog--sidebar .etnos-widget-popular--image.image {
  min-width: 33%;
}
@media screen and (min-width: 576px) {
  .etnos-blog--sidebar .etnos-widget-popular--image.image {
    min-width: 30px;
  }
}
.etnos-blog--sidebar .etnos-widget-popular--image span {
  height: 30px;
  width: 30px;
  color: var(--c-light);
  background: var(--c-active);
  border-radius: 50%;
  position: absolute;
  top: 0;
  font-weight: 600;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}
.etnos-blog--sidebar .etnos-widget-popular--image img {
  border-radius: 50%;
}
@media screen and (min-width: 576px) {
  .etnos-blog--sidebar .etnos-widget-popular--image img {
    display: none;
  }
}
.etnos-blog--sidebar .etnos-widget-popular--author {
  font-size: 0.9rem;
}
.etnos-blog--sidebar .etnos-widget-popular--content a > * {
  transition: all 0.35s ease;
}
.etnos-blog--sidebar .etnos-widget-popular--content a:hover > * {
  color: var(--c-active);
}
.etnos-blog--sidebar .etnos-widget-popular--content span:first-of-type::after {
  content: "―";
  margin-right: 5px;
  margin-left: 5px;
}
.etnos-blog--sidebar .etnos-widget-reading--item {
  display: flex;
}
.etnos-blog--sidebar .etnos-widget-reading--item.item-1 {
